<template>
	<div class="home_view">
		<div class="projectTitle">欢迎使用 {{projectName}}</div>
		<div class="count_list">
			<el-collapse-transition v-if="btnAuth('zhaopinxinxi','首页总数')">
				<el-card v-show="countTypeList.closezhaopinxinxiCountType" class="card_view">
					<template #header>
						<div class="index_card_head">
							<div class="card_head_title">
								招聘信息
							</div>
							<div class="card_head_right">
								<el-icon @click="countTypeClick('hiddenzhaopinxinxiCountType')" class="showIcons"
									:class="countTypeList.hiddenzhaopinxinxiCountType?'showIcons1':''">
									<ArrowUpBold />
								</el-icon>
								<el-icon @click="countTypeClick('closezhaopinxinxiCountType')" class="closeIcons">
									<CloseBold />
								</el-icon>
							</div>
							
						</div>
					</template>
					<el-collapse-transition>
						<div class="count_item" v-show="countTypeList.hiddenzhaopinxinxiCountType">
							<div class="count_title">招聘信息总数</div>
							<div class="count_num">{{zhaopinxinxiCount}}</div>
						</div>
					</el-collapse-transition>
				</el-card>
			</el-collapse-transition>
		</div>
		<div class="card_list">
			<el-collapse-transition v-if="btnAuth('zhaopinxinxi','首页统计')">
				<el-card v-show="cardTypeList.closezhaopinxinxiChartType1" class="card_view">
					<template #header>
						<div class="index_card_head">
							<div class="card_head_title">
								招聘信息
							</div>
							<div class="card_head_right">
								<el-icon @click="cardTypeClick('hiddenzhaopinxinxiChartType1')" class="showIcons"
									:class="cardTypeList.hiddenzhaopinxinxiChartType1?'showIcons1':''">
									<ArrowUpBold />
								</el-icon>
								<el-icon @click="cardTypeClick('closezhaopinxinxiChartType1')" class="closeIcons">
									<CloseBold />
								</el-icon>
							</div>
						</div>
					</template>
					<el-collapse-transition>
						<div class="card_item" v-show="cardTypeList.hiddenzhaopinxinxiChartType1">
							<div id="zhaopinxinxigangweileixingEchart1" style="width: 100%;height: 400px;"></div>
						</div>
					</el-collapse-transition>
				</el-card>
			</el-collapse-transition>
			<el-collapse-transition v-if="btnAuth('zhaopinxinxi','首页统计')">
				<el-card v-show="cardTypeList.closezhaopinxinxiChartType2" class="card_view">
					<template #header>
						<div class="index_card_head">
							<div class="card_head_title">
								招聘信息
							</div>
							<div class="card_head_right">
								<el-icon @click="cardTypeClick('hiddenzhaopinxinxiChartType2')" class="showIcons"
									:class="cardTypeList.hiddenzhaopinxinxiChartType2?'showIcons1':''">
									<ArrowUpBold />
								</el-icon>
								<el-icon @click="cardTypeClick('closezhaopinxinxiChartType2')" class="closeIcons">
									<CloseBold />
								</el-icon>
							</div>
						</div>
					</template>
					<el-collapse-transition>
						<div class="card_item" v-show="cardTypeList.hiddenzhaopinxinxiChartType2">
							<div id="zhaopinxinxidiquEchart2" style="width: 100%;height: 400px;"></div>
						</div>
					</el-collapse-transition>
				</el-card>
			</el-collapse-transition>
			<el-collapse-transition v-if="btnAuth('zhaopinxinxi','首页统计')">
				<el-card v-show="cardTypeList.closezhaopinxinxiChartType3" class="card_view">
					<template #header>
						<div class="index_card_head">
							<div class="card_head_title">
								招聘信息
							</div>
							<div class="card_head_right">
								<el-icon @click="cardTypeClick('hiddenzhaopinxinxiChartType3')" class="showIcons"
									:class="cardTypeList.hiddenzhaopinxinxiChartType3?'showIcons1':''">
									<ArrowUpBold />
								</el-icon>
								<el-icon @click="cardTypeClick('closezhaopinxinxiChartType3')" class="closeIcons">
									<CloseBold />
								</el-icon>
							</div>
						</div>
					</template>
					<el-collapse-transition>
						<div class="card_item" v-show="cardTypeList.hiddenzhaopinxinxiChartType3">
							<div id="zhaopinxinxixinzidaiyuEchart3" style="width: 100%;height: 400px;"></div>
						</div>
					</el-collapse-transition>
				</el-card>
			</el-collapse-transition>
		</div>
	</div>
</template>

<script setup>
	import {
		inject,
		nextTick,
		ref,
		getCurrentInstance
	} from 'vue';
	const context = getCurrentInstance()?.appContext.config.globalProperties;
	const projectName = context.$project.projectName
	const countTypeList = ref({
	})
	const getCountList=()=>{
		countTypeList.value.closezhaopinxinxiCountType = true
		countTypeList.value.hiddenzhaopinxinxiCountType = true
		if(btnAuth('zhaopinxinxi','首页总数')){
			getzhaopinxinxiCount()
		}
	}
	const zhaopinxinxiCount = ref(0)
	const getzhaopinxinxiCount = () => {
		context?.$http({
			url:'zhaopinxinxi/count',
			method: 'get'
		}).then(res=>{
			zhaopinxinxiCount.value = res.data.data
		})
	}
	const countTypeClick = (e) => {
		countTypeList.value[e] = !countTypeList.value[e]
	}
	const init=()=>{
		getCountList()
		getCardList()
	}
	//权限验证
	const btnAuth = (e,a)=>{
		return context?.$toolUtil.isAuth(e,a)
	}
	let echarts = inject("echarts")
	const cardTypeClick = (e) =>{
		cardTypeList.value[e] = !cardTypeList.value[e]
		setTimeout(()=>{
			getCardList()
		},1000)
	}
	const cardTypeList = ref({
		closezhaopinxinxiChartType1: true,
		hiddenzhaopinxinxiChartType1: true,
		closezhaopinxinxiChartType2: true,
		hiddenzhaopinxinxiChartType2: true,
		closezhaopinxinxiChartType3: true,
		hiddenzhaopinxinxiChartType3: true,
	})
	const getCardList = () => {
		if(btnAuth('zhaopinxinxi','首页统计')){
			getzhaopinxinxiChart1()
		}
		if(btnAuth('zhaopinxinxi','首页统计')){
			getzhaopinxinxiChart2()
		}
		if(btnAuth('zhaopinxinxi','首页统计')){
			getzhaopinxinxiChart3()
		}
	}
	const getzhaopinxinxiChart1 = () => {
		nextTick(()=>{
			var gangweileixingEchart1 = echarts.init(document.getElementById("zhaopinxinxigangweileixingEchart1"),'macarons');
			context?.$http({
				url: "zhaopinxinxi/group/gangweileixing",
				method: "get",
			}).then(obj=>{
				let res = obj.data.data
				let xAxis = [];
				let yAxis = [];
				let pArray = []
				for(let i=0;i<res.length;i++){
				    xAxis.push(res[i].gangweileixing);
				    yAxis.push(parseFloat((res[i].total)));
				    pArray.push({
				        value: parseFloat((res[i].total)),
				        name: res[i].gangweileixing
				    })
				}
				var option = {};
                option = {
                    title: {
                        text: '岗位类型统计',
                        left: 'center'
                    },
                    legend: {
                        orient: 'vertical',
                        left: 'left'
                    },
                    tooltip: {
						trigger: 'item',
						formatter: '{b} : {c} ({d}%)'
                    },
                    series: [
                        {
                            type: 'pie',
                            radius: '55%',
                            center: ['50%', '60%'],
                            data: pArray,
                            emphasis: {
                                itemStyle: {
                                    shadowBlur: 10,
                                    shadowOffsetX: 0,
									shadowColor: 'rgba(0, 0, 0, 0.5)'
                                }
                            }
                        }
                    ]
                };
				gangweileixingEchart1.clear()
				// 使用刚指定的配置项和数据显示图表。
				gangweileixingEchart1.setOption(option);
				//根据窗口的大小变动图表
				gangweileixingEchart1.resize();
			})
		})
	}
	const getzhaopinxinxiChart2 = () => {
		nextTick(()=>{
			var diquEchart2 = echarts.init(document.getElementById("zhaopinxinxidiquEchart2"),'macarons');
			context?.$http({
				url: "zhaopinxinxi/group/diqu",
				method: "get",
			}).then(obj=>{
				let res = obj.data.data
				let xAxis = [];
				let yAxis = [];
				let pArray = []
				for(let i=0;i<res.length;i++){
				    xAxis.push(res[i].diqu);
				    yAxis.push(parseFloat((res[i].total)));
				    pArray.push({
				        value: parseFloat((res[i].total)),
				        name: res[i].diqu
				    })
				}
				var option = {};
                option = {
                    title: {
                        text: '城市统计',
                        left: 'center'
                    },
                    legend: {
                        orient: 'vertical',
                        left: 'left'
                    },
                    tooltip: {
						trigger: 'item',
						formatter: '{b} : {c} ({d}%)'
                    },
                    series: [
                        {
                            type: 'pie',
                            radius: ['25%', '55%'],
                            center: ['50%', '60%'],
                            data: pArray,
                            emphasis: {
                                itemStyle: {
                                    shadowBlur: 10,
                                    shadowOffsetX: 0,
									shadowColor: 'rgba(0, 0, 0, 0.5)'
                                }
                            }
                        }
                    ]
                };
				// 使用刚指定的配置项和数据显示图表。
				diquEchart2.clear()
				diquEchart2.setOption(option);
				//根据窗口的大小变动图表
				diquEchart2.resize();
			})
		})
	}
	const getzhaopinxinxiChart3 = () => {
		nextTick(()=>{
        //  xinzidaiyu
			var xinzidaiyuEchart3 = echarts.init(document.getElementById("zhaopinxinxixinzidaiyuEchart3"),'macarons');
			context?.$http({
				url: `zhaopinxinxi/value/gangweimingcheng/xinzidaiyu`,
				method: "get",
			}).then(obj=>{
				let res = obj.data.data
				let xAxis = [];
				let yAxis = [];
				let pArray = []
				for(let i=0;i<res.length;i++){
				    xAxis.push(res[i].gangweimingcheng);
				    yAxis.push(parseFloat((res[i].total)));
				    pArray.push({
				        value: parseFloat((res[i].total)),
				        name: res[i].gangweimingcheng
				    })
				}
				var option = {};
                option = {
                    title: {
                        text: '薪资统计',
                        left: 'center'
                    },
                    tooltip: {
                      trigger: 'item',
                      formatter: '{b} : {c}'
                    },
                    xAxis: {
                        type: 'category',
                        data: xAxis,
                        axisLabel: {
                            rotate: 40
                        }
                    },
                    yAxis: {
                        type: 'value'
                    },
                    series: [{
                        data: yAxis,
                        type: 'bar'
                    }]
                };
				// 使用刚指定的配置项和数据显示图表。
				xinzidaiyuEchart3.clear()
				xinzidaiyuEchart3.setOption(option);
				//根据窗口的大小变动图表
				xinzidaiyuEchart3.resize();
			})
		})
	}
	init()
</script>
<style lang="scss">
	.projectTitle{
		padding: 0 0 20px;
		font-weight: bold;
		display: flex;
		width: 100%;
		font-size: 30px;
		justify-content: center;
		align-items: center;
		height: 50%;
	}

	.showIcons {
		transition: transform 0.3s;
		margin-right: 10px;
	}

	.showIcons1 {
		transform: rotate(-180deg);
	}
	
	// 总数盒子
	.count_list{
		padding: 0 0 20px;
		display: flex;
		width: 100%;
		justify-content: center;
		align-items: flex-start;
		flex-wrap: wrap;
		// 总数card
		.card_view {
			border: 1px solid #e4e7ed;
			box-shadow: 0px 0px 12px rgba(0,0,0,.12);
			padding: 0 0 50px;
			margin: 0 10px 10px;
			flex: 1;
			background: rgba(0,123,213,1);
			width: 100%;
			position: relative;
			box-sizing: border-box;
			height: auto;
			// card头部
			.el-card__header {
				border: 1px solid #e4e7ed;
				padding: 10px;
				left: 0;
				bottom: 0;
				width: 100%;
				border-width: 1px 0 0;
				position: absolute;
				// 头部盒子
				.index_card_head {
					display: flex;
					width: 100%;
					justify-content: space-between;
					align-items: center;
					// 标题
					.card_head_title {
						color: #fff;
						font-size: 14px;
					}
					// 按钮盒子
					.card_head_right {
						display: flex;
						align-items: center;
						// 按钮
						.el-icon {
							cursor: pointer;
							color: #fff;
							font-size: 20px;
						}
					}
				}
			}
			// body
			.el-card__body {
				padding: 0;
				// body盒子
				.count_item{
					padding: 30px;
					display: flex;
					align-items: center;
					text-align: center;
					// 总数标题
					.count_title{
						padding: 0 10px 0 0;
						color: #fff;
						font-weight: bold;
						font-size: 20px;
						line-height: 3;
					}
					// 总数数字
					.count_num{
						color: #fff;
						font-weight: bold;
						font-size: 24px;
						line-height: 3;
					}
				}
			}
		}
	}
	// 首页盒子
	.home_view {
		padding: 30px 0;
		background: url(http://clfile.zggen.cn/20240203/b89de8d5b5894c14925e21e6f1cbfff7.png) no-repeat center top / 100% 100% !important;
		width: 100%;
		min-height: 100vh;
		height: auto;
	}
	// 统计图盒子
	.card_list {
		padding: 0 0 20px;
		display: flex;
		width: 100%;
		flex-wrap: wrap;
		// 统计图card
		.card_view {
			border: 0px solid #e4e7ed;
			box-shadow: none;
			margin: 0 10px 20px;
			background: #fff;
			width: 32%;
			box-sizing: border-box;
			height: auto;
			// 头部
			.el-card__header {
				border: 1px solid #e4e7ed;
				display: none;
				width: 100%;
				border-width: 0 0 1px;
				// 头部盒子
				.index_card_head {
					display: flex;
					width: 100%;
					justify-content: space-between;
					align-items: center;
					// 标题
					.card_head_title {
						color: #f00;
						font-size: 14px;
					}
					// 按钮盒子
					.card_head_right {
						display: flex;
						align-items: center;
						// 按钮
						.el-icon{
							cursor: pointer;
							color: #aaa;
							font-size: 20px;
						}
					}
				}
			}
			// body
			.el-card__body {
				padding: 0;
				width: 100%;
				// body盒子
				.card_item{
					padding: 30px;
					text-align: center;
				}
			}
		}
	}
</style>
